import { audiosFolder, imagesFolder, path, thumbsFolder, wordmapsFolder } from "../../util/constants";
import { wordmaps } from "../../stores/wordmap";
const fs = require("fs");
export function save(wordmap) {
    fs.writeFileSync(path(`${wordmapsFolder}/${wordmap.id}.wordmap.json`), JSON.stringify(wordmap));
}
export function moveAudioToWordViewFolder(audioPath, wordmapId) {
    fs.copyFileSync(audioPath, path(`${audiosFolder}/${wordmapId}`));
}
export function moveThumbToWordViewFolder(thumbPath, wordmapId) {
    fs.copyFileSync(thumbPath.replace("file://", ""), path(`${thumbsFolder}/${wordmapId}`));
}
export function thumbWasSet(thumb) {
    return thumb.length > 0;
}
export function allImagePointsOf(uuid) {
    let result = [];
    let unsub = wordmaps.subscribe(wordmaps => {
        for (const wordmap of wordmaps) {
            if (wordmap.id == uuid) {
                for (const point of wordmap.points) {
                    result.push(point.uuid);
                }
            }
        }
    });
    unsub();
    return result;
}
export function wordmapOf(uuid) { return path(`${wordmapsFolder}/${uuid}.wordmap.json`); }
export function audioOf(uuid) { return path(`${audiosFolder}/${uuid}`); }
export function thumbOf(uuid) { return path(`${thumbsFolder}/${uuid}`); }
export function imageFor(uuid) { return path(`${imagesFolder}/${uuid}`); }
